            <div class="info fl" @click="outgoing">
              <div class="ziti mar">外送</div>
              <div class="zaixian mar">三十分钟必达</div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="hot-product">
      <div class="hot-product-title">热销推荐</div>
      <div class="product-box clearfix">
        <div class="product-item fl clearfix" v-for="(item, index) in hotProducts" :key="index">
          <div class="p-img" @click="lookinfo(item.pid)">
            <img class="auto-img" :src="item.smallImg" />
          </div>
          <div class="pro-name one-text">{{item.name}}</div>
          <div class="pro-price fl">￥{{item.price}}</div>
          <div class="pro-icon fr">
            <img src="../../assets/images/share.png" alt class="auto-img" />
            <van-cell @click="showShare = true" />
            <van-share-sheet
              v-model="showShare"
              title="立即分享给好友"
              :options="options"
              @select="share"
            />
          </div>
        </div>
      </div>
      <div class="scope clearfix">
        <div class="scope-item fl clearfix" @click="grinding">
          <div class="scopeimg fl">
            <img src="../../assets/images/scope1.jpg" alt class="auto-img" />
          </div>
          <div class="scope-text fl">
            <div class="scope-top">现磨咖啡</div>
            <div class="scope-button">味道纯正,保障新鲜</div>
          </div>
        </div>
        <div class="scope-item fr clearfix" @click="sweets">
          <div class="scopeimg fl">
            <img src="../../assets/images/scope2.jpg" alt class="auto-img" />
          </div>
          <div class="scope-text fl">
            <div class="scope-top">自制甜点</div>
            <div class="scope-button">进口奶油,独家秘方</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../../assets/less/home.less";
import { utils } from "../../assets/js/utils";
export default {
  name: "Home",
  data() {
    return {
      hotProducts: [],
      showShare: false,
      options: [
        { name: "微信", icon: "wechat" },
        { name: "微博", icon: "weibo" },
        { name: "复制链接", icon: "link" },
        { name: "分享海报", icon: "poster" },
        { name: "二维码", icon: "qrcode" },
      ],
      show: false,
    };
  },
  created() {
    this. recommendation();
  },
  methods: {
    // 推荐商品
     recommendation() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/typeProducts",
        params: {
          appkey: this.appkey,
          key: "isHot",
          value: 1,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 500) {
            this.hotProducts = result.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    // 查看详情
    lookinfo(pid) {
      this.$router.push({ name: "Detail", query: { pid } });
    },

    // 分享
    share(option) {
      this.$toast(option.name);
      this.showShare = false;
    },
    //自提地址目的地
    destination() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        loadingType: "spinner",
      });
      this.$router.push({ name: "Destination" });
      setTimeout(() => {
        this.$toast.clear();
      }, 500);
    },
    //现磨咖啡
    grinding() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        loadingType: "spinner",
      });
      this.$router.push({ name: "Grinding" });
      setTimeout(() => {
        this.$toast.clear();
      }, 500);
    },
    //自制甜点
    sweets() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.$router.push({ name: "Sweets" });
      setTimeout(() => {
        this.$toast.clear();
      }, 500);
    },

    //自送
    outgoing() {
      this.$dialog({ message: '请先去添加购物商品' });
    },
  },
};
</script>

